#news {
  display: none;
  justify-content: column; }

#newsroot {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: space-between;
  padding-top: 20px; }

.card {
  position: relative;
  display: flex;
  flex: 1 1 30%;
  max-width: 520px;
  max-height: 400px;
  min-width: 290px;
  min-height: 220px;
  margin: 20px;
  color: #ffffff;
  background-color: #00ffff; }

.card-image, .card-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.3; }

.info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 20px; }

.card-link, .info {
  z-index: 999; }

.title {
  margin-bottom: 50px;
  font-size: calc(16px + 8 * ((100vw - 320px) / 700)); }

.description {
  font-size: calc(10px + 8 * ((100vw - 320px) / 960)); }

.date {
  margin-top: 15px;
  text-align: end;
  font-size: calc(7px + 8 * ((100vw - 320px) / 960)); }
